import React, { Component } from 'react';
import { Utils } from '../../services/Utils';
import { ILottoService } from '../../services/Interfaces';
import Flip from '../Farm/Flip';

interface ILottoRowProps {
  lottoService?: ILottoService;
  onApproveClick: (lottoService: ILottoService) => void;
  onBuyTicket: (lottoService: ILottoService) => void;
  onCloseDraw: (lottoService: ILottoService) => void;
  onClaimClick: (lottoService: ILottoService) => void;
}

interface ILottoRowState {
  dropdownActiveClass: string;
}

class LottoRow extends Component<ILottoRowProps, ILottoRowState> {
  dropdownBody: React.RefObject<HTMLDivElement>;
  countdownTimer?: NodeJS.Timer;

  constructor(props: ILottoRowProps) {
    super(props);

    this.dropdownBody = React.createRef();

    this.state = {
      dropdownActiveClass: ''
    };

    this.toggleDropdown = this.toggleDropdown.bind(this);
  }

  approve = () => {
    this.props.onApproveClick(this.props.lottoService!);
  };
  buy = () => {
    this.props.onBuyTicket(this.props.lottoService!);
  };
  close = () => {
    this.props.onCloseDraw(this.props.lottoService!);
  };
  claim = () => {
    this.props.onClaimClick(this.props.lottoService!);
  };

  toggleDropdown(e: any) {
    if (this.dropdownBody!.current!.contains(e.relatedTarget)) return;

    this.setState({
      dropdownActiveClass: this.state.dropdownActiveClass === '' ? 'is-active' : ''
    });
  }

  tickerClass() {
    if (+this.props.lottoService!.currentDrawCountdownTime.days > 0) {
      return 'days';
    }
    if (+this.props.lottoService!.currentDrawCountdownTime.hours > 0) {
      return 'hours';
    }
    if (+this.props.lottoService!.currentDrawCountdownTime.mins > 0) {
      return 'mins';
    }
  }

  render() {
    return (
      <tr>
        <td>
          <div className="double_cell">
            <div className="tLogo">
              <div className="tLogo_group">
                <img
                  alt={this.props.lottoService!.config.token}
                  className="token"
                  src={this.props.lottoService!.config.tokenIcon}
                />
              </div>
            </div>
          </div>
        </td>
        <td>
          <div className="cell">
            <div className="cell_PrimaryText">{this.props.lottoService!.config.token}</div>
          </div>
        </td>
        <td>
          <div className="cell">
            <div className="cell_TitleText">Ticket Price</div>
            <div className="cell_SecondaryText">
              <span className="value px-1">{Utils.toFixed(this.props.lottoService!.ticketPrice, 2)}</span>{' '}
              {this.props.lottoService!.config.token}
            </div>
          </div>
        </td>
        <td>
          <div className="cell">
            <div className="cell_TitleText">Prizes</div>
            <div
              className={
                'cell_SecondaryText ' +
                (this.props.lottoService!.unclaimedPrizes.length > 0 ? 'has-action-text has-text-weight-semibold' : '')
              }>
              <span className="value px-1">
                <span className="value px-1">{Utils.toFixed(this.props.lottoService!.unclaimedPrizes.length, 0)}</span>
              </span>
              Wins
            </div>
          </div>
        </td>
        <td>
          <div className="cell">
            <div className="cell_TitleText">Tickets in Draw</div>
            <div
              className={
                'cell_SecondaryText ' +
                (this.props.lottoService!.unclaimedPrizes.length > 0 ? 'has-action-text has-text-weight-semibold' : '')
              }>
              <span className="value px-1">
                <span className="value px-1">{Utils.toFixed(this.props.lottoService!.currentDrawTickets, 0)}</span>
              </span>
              Tickets
            </div>
          </div>
        </td>
        <td>
          <div className="cell">
            <React.Fragment>
              <div className="countdown-container is-flex is-flex-direction-row is-justify-content-center pt-2">
                <div className="countdown is-flex is-flex-direction-row is-justify-content-space-between">
                  <div className={'ticker ' + this.tickerClass()}>
                    <div className="digits">
                      <Flip value={this.props.lottoService!.currentDrawCountdownTime.hours} />
                    </div>
                    <div>
                      <div className="units">hours</div>
                    </div>
                  </div>
                  <div className={'ticker ' + this.tickerClass()}>
                    <div className="digits">
                      <Flip value={this.props.lottoService!.currentDrawCountdownTime.mins} />
                    </div>
                    <div>
                      <div className="units">mins</div>
                    </div>
                  </div>
                </div>
              </div>
            </React.Fragment>
          </div>
        </td>
        <td>
          <div className="has-text-centered">
            <div>{this.renderActionDropDown()}</div>
          </div>
        </td>
      </tr>
    );
  }

  renderActionDropDown() {
    if (this.props.lottoService!.config.expired && this.props.lottoService!.unclaimedPrizes.length <= 0) {
      return <div className="cell_PrimaryText is-fullwidth has-text-danger has-text-weight-semibold">Expired!</div>;
    } else {
      return (
        <div className={`action-dropdown dropdown ${this.state.dropdownActiveClass}`}>
          <div className="dropdown-trigger">
            <button
              className="button is-action"
              aria-haspopup="true"
              aria-controls="dropdown-menu"
              onBlur={this.toggleDropdown}
              onFocus={this.toggleDropdown}>
              <span>Action</span>
              <span className="icon is-small">
                <i className="fas fa-angle-down" aria-hidden="true"></i>
              </span>
            </button>
          </div>
          <div
            className="dropdown-menu"
            id="dropdown-menu"
            role="menu"
            ref={this.dropdownBody}
            onClick={this.toggleDropdown}>
            <div className="dropdown-content">
              {!this.props.lottoService!.isSpendApproved && (
                <button className="button is-link is-transparent" onClick={this.approve}>
                  <span>Approve</span>
                </button>
              )}
              {this.props.lottoService!.isSpendApproved && !this.props.lottoService!.currentDrawDeadlinePassed && (
                <button className="button is-link is-transparent" onClick={this.buy}>
                  <span>{this.props.lottoService!.actionDisplayName}</span>
                </button>
              )}
              {this.props.lottoService!.isSpendApproved && this.props.lottoService!.currentDrawDeadlinePassed && (
                <button className="button is-link is-transparent" onClick={this.close}>
                  <span>Close Draw</span>
                </button>
              )}
              {this.props.lottoService!.unclaimedPrizes.length > 0 && (
                <button className="button is-link is-transparent" onClick={this.claim}>
                  <span>Claim Prize</span>
                </button>
              )}
            </div>
          </div>
        </div>
      );
    }
  }
}

export default LottoRow;
