import { Component } from 'react';
import { Subscription } from 'rxjs';
import { ILottoService } from '../../services/Interfaces';
import LottoRow from './LottoRow';

interface ILottoProps {
  lottoServices: ILottoService[];
}

enum Action {
  None = 1,
  Approve,
  Buy,
  Claim
}

interface ILottoState {
  lottoService?: ILottoService | null;
  tickets: number;
  action: Action;
  showDetail: boolean;
}

class Lotto extends Component<ILottoProps, ILottoState> {
  subscription: Subscription;
  walletAddress?: string;
  chainId?: number;

  constructor(props: any) {
    super(props);

    this.state = {
      lottoService: null,
      tickets: 0,
      action: Action.None,
      showDetail: false
    };

    this.buyTickets = this.buyTickets.bind(this);
    this.claimPrize = this.claimPrize.bind(this);
    this.clearAction = this.clearAction.bind(this);
    this.setAmount = this.setAmount.bind(this);
    this.approveSpend = this.approveSpend.bind(this);
    this.confirmBuy = this.confirmBuy.bind(this);
    this.closeDraw = this.closeDraw.bind(this);
    this.confirmClaimPrize = this.confirmClaimPrize.bind(this);
    this.handleAmountToBuyChange = this.handleAmountToBuyChange.bind(this);

    this.subscription = new Subscription();
  }

  buyTickets(lottoService: ILottoService) {
    this.setState({
      action: Action.Buy,
      lottoService: lottoService,
      tickets: 0
    });
  }

  claimPrize(lottoService: ILottoService) {
    this.setState({
      action: Action.Claim,
      lottoService: lottoService,
      tickets: 0
    });
  }

  clearAction() {
    this.setState({ action: Action.None, lottoService: null, tickets: 0 });
  }

  setAmount(e: any) {
    this.setState({ tickets: e.target.value });
  }

  approveSpend(lottoService: ILottoService) {
    lottoService!.approve();
    this.clearAction();
  }

  confirmBuy() {
    this.state.lottoService!.buyTicket(this.state.tickets);
    this.clearAction();
  }

  closeDraw(lottoService: ILottoService) {
    lottoService!.closeDraw();
    this.clearAction();
  }

  confirmClaimPrize() {
    this.state.lottoService!.claimPrize(this.state.tickets);
    this.clearAction();
  }

  handleAmountToBuyChange(changeBy: number) {
    this.setState({ tickets: this.state.tickets + changeBy });
  }

  render() {
    return (
      <div className="tile is-ancestor mx-3">
        <div className="tile is-vertical is-12">
          <div className="tile is-parent">
            <article className="card tile is-child box has-background-light">
              {!this.state.lottoService && <div className="content">{this.renderPools()}</div>}
              {this.state.lottoService && this.state.action === Action.Buy && (
                <div className="narrow-content">{this.renderBuyTicket()}</div>
              )}
              {/* {this.state.lottoService && this.state.action === Action.Claim && (
                <div className="narrow-content">{this.renderClaimPrize()}</div>
              )} */}
            </article>
          </div>
        </div>
      </div>
    );
  }

  renderPools() {
    return (
      <div>
        <div className="table-control">
          <table className="table small-text is-fullwidth">
            <thead>
              <tr>
                <th className="image-header"></th>
                <th>Lotto</th>
                <th>Ticket Price</th>
                <th>Prizes</th>
                <th>Tickets in Draw</th>
                <th>Countdown</th>
                <th></th>
              </tr>
            </thead>
            <tbody>
              {this.props.lottoServices.map(ls => (
                <LottoRow
                  key={ls.config.lottoContractAddress}
                  onApproveClick={this.approveSpend}
                  onBuyTicket={this.buyTickets}
                  onCloseDraw={this.closeDraw}
                  onClaimClick={this.claimPrize}
                  lottoService={ls}></LottoRow>
              ))}
            </tbody>
          </table>
        </div>
      </div>
    );
  }

  renderBuyTicket() {
    return (
      <div className="is-flex my-5">
        <div className="is-flex is-flex-direction-column is-align-items-center is-justify-content-center character-mint">
          <ul>
            <li className="action-form">
              <div className="field has-addons">
                <div className="control">
                  <button
                    className="button minus-button is-action"
                    disabled={this.state.tickets <= 1}
                    onClick={() => this.handleAmountToBuyChange(-1)}>
                    <span className="icon">
                      <i className="fas fa-minus"></i>
                    </span>
                  </button>
                </div>
                <div className="control">
                  <input className="input is-action" type="text" disabled={true} value={this.state.tickets} />
                </div>
                <div className="control">
                  <button
                    className="button plus-button is-action"
                    disabled={this.state.tickets >= 20}
                    onClick={() => this.handleAmountToBuyChange(+1)}>
                    <span className="icon">
                      <i className="fas fa-plus"></i>
                    </span>
                  </button>
                </div>
              </div>
            </li>
            <li>
              <button className="button boost-button is-action is-fullwidth" onClick={this.confirmBuy}>
                Buy Tickets
              </button>
            </li>
          </ul>

          <div className="is-flex is-flex-direction-column is-align-items-center is-justify-content-center mt-5">
            <p>
              <span className="mr-1">Cost {+this.state.lottoService!.ticketPrice * this.state.tickets}</span>
              {this.state.lottoService!.config.token} (+ gas)
            </p>
          </div>
        </div>
      </div>
    );
  }
}

export default Lotto;
