import { constants, ethers } from 'ethers';
import { EthereumService } from './EthereumService';
import { MessagingService } from './MessagingService';
import { Utils } from './Utils';
import { ILottoService } from './Interfaces';
import { ILottoConfig } from '../models/LottoConfig';

export class LottoServiceV1 implements ILottoService {
  walletAddress?: string;

  tokenContract?: ethers.Contract | any;
  lottoContract?: ethers.Contract | any;

  currentDrawIndex = 0;
  currentDrawDeadlinePassed = false;
  currentDrawTickets = 0;
  currentDrawCountdownSecs = 0;
  currentDrawCountdownTime = { days: '  ', hours: '  ', mins: '  ', secs: '  ', isComplete: false };

  ticketPrice: string | 0 = 0;
  isSpendApproved: boolean = false;
  unclaimedPrizes: [] = [];

  actionDisplayName = 'Buy Ticket';

  constructor(
    public config: ILottoConfig,
    private ethereumService: EthereumService,
    private messagingService: MessagingService
  ) {
    this.walletAddress = this.ethereumService!.walletData.walletAddress;
    this.tokenContract = new ethers.Contract(
      this.config.tokenContractAddress,
      this.config.tokenContractAbi,
      ethereumService.signer
    );
    this.lottoContract = new ethers.Contract(
      this.config.lottoContractAddress,
      this.config.lottoContractAbi,
      ethereumService.signer
    );

    this.setupEventHandlers();
  }

  async update(): Promise<ILottoService> {
    const currentDraw = await this.lottoContract.getCurrentDraw();
    this.currentDrawIndex = currentDraw.toNumber();

    var work = [
      this.tokenContract.allowance(this.walletAddress, this.config.lottoContractAddress),
      this.lottoContract.ticketPrice(),
      this.lottoContract.draws(currentDraw),
      this.lottoContract.getUnclaimedWinnerByAddress(this.walletAddress),
      this.lottoContract.getNumberOfTicketPerAddressByDraw(currentDraw, this.walletAddress)
    ];

    return Promise.all(work).then(response => {
      this.isSpendApproved = response[0]._hex !== constants.Zero._hex;
      this.ticketPrice = Utils.formatUnits(response[1], 18);
      const networkBlockTime = this.ethereumService.networkManager!.network!.blocktime;
      const deadlineBlockNumber = +Utils.formatUnits(response[2][4], 0);
      this.currentDrawCountdownSecs =
        networkBlockTime * (deadlineBlockNumber - this.ethereumService.walletData.blockNumber);
      this.updateTimeRemaining();
      this.currentDrawDeadlinePassed = deadlineBlockNumber < this.ethereumService.walletData.blockNumber;
      this.unclaimedPrizes = response[3];
      this.currentDrawTickets = response[4];
      return this;
    });
  }

  updateTimeRemaining() {
    var secondsRemaining = this.currentDrawCountdownSecs;

    this.currentDrawCountdownTime = {
      days: secondsRemaining < 0 ? '00' : ('0' + Math.floor(secondsRemaining / (60 * 60 * 24))).slice(-2),
      hours: secondsRemaining < 0 ? '00' : ('0' + Math.floor((secondsRemaining / (60 * 60)) % 24)).slice(-2),
      mins: secondsRemaining < 0 ? '00' : ('0' + Math.floor((secondsRemaining / 60) % 60)).slice(-2),
      secs: secondsRemaining < 0 ? '00' : ('0' + (secondsRemaining % 60)).slice(-2),
      isComplete: secondsRemaining < 0
    };
  }

  approve() {
    var amount = Utils.parseUnits('1000000000');

    this.tokenContract.approve(this.config.lottoContractAddress, amount).then(
      (approveResponse: any) =>
        this.messagingService.events.next({
          message: `Approval of ${this.config.token} lotto is pending`,
          pending: true
        }),
      (error: any) =>
        this.messagingService.errors.next({
          message: `Error approving ${this.config.token} : (${Utils.getContractErrorMessage(error)})`,
          error: error
        })
    );
  }

  buyTicket(amount: any) {
    var sanitizedAmount = Utils.sanitizeParseUnits(amount);

    this.lottoContract.buyTicket(sanitizedAmount).then(
      (stakeResponse: any) =>
        this.messagingService.events.next({
          message: `Purchase of ${this.config.token} lotto ticket is pending`,
          pending: true
        }),
      (error: any) =>
        this.messagingService.errors.next({
          message: `Error purchasing ${this.config.token} lotto ticket: (${Utils.getContractErrorMessage(error)})`,
          error: error
        })
    );
  }

  closeDraw() {
    this.lottoContract.closeDraw().then(
      (withdrawResponse: any) =>
        this.messagingService.events.next({
          message: `Closing ${this.config.token} lotto draw is pending`,
          pending: true
        }),
      (error: any) =>
        this.messagingService.errors.next({
          message: `Error closing  ${this.config.token} lotto draw : (${Utils.getContractErrorMessage(error)})`,
          error: error
        })
    );
  }

  claimPrize(prizeIndex: number) {
    this.lottoContract.claim(prizeIndex).then(
      (claimResponse: any) =>
        this.messagingService.events.next({
          message: `Claim of lotto prize is pending`,
          pending: true
        }),
      (error: any) =>
        this.messagingService.errors.next({
          message: `Error claiming lotto prize reward : (${Utils.getContractErrorMessage(error)})`,
          error: error
        })
    );
  }

  removeEventHandlers() {
    if (this.tokenContract) {
      this.tokenContract!.removeAllListeners();
    }

    if (this.lottoContract) {
      this.lottoContract!.removeAllListeners();
    }
  }

  setupEventHandlers() {
    this.removeEventHandlers();

    var update = (message: string) =>
      this.update().then(() => {
        this.messagingService.events.next({ message: message });
      });

    if (this.tokenContract) {
      this.tokenContract!.on(
        {
          address: this.ethereumService.walletData.walletAddress,
          topics: [ethers.utils.id('Approval(address,address,uint256)')]
        },
        (owner: any, spender: any, amount: any) => {
          if (owner === this.ethereumService.walletData.walletAddress) {
            update(`Approved ${this.config.token} Lotto`);
          }
        }
      );
    }

    if (this.lottoContract) {
      this.lottoContract!.on(
        {
          address: this.ethereumService.walletData.walletAddress,
          topics: [ethers.utils.id('TicketPurchased(address,uint256,uint256)')]
        },
        (user: any, poolId: any, amount: any) => {
          if (user === this.ethereumService.walletData.walletAddress) {
            update(`${Utils.formatUnits(amount, 18)} ${this.config.token} tickets purchased`);
          }
        }
      );

      this.lottoContract!.on(
        {
          address: this.ethereumService.walletData.walletAddress,
          topics: [ethers.utils.id('PrizeClaimed(address,uint256,uint256)')]
        },
        (user: any, block: any, amount: any) => {
          if (user === this.ethereumService.walletData.walletAddress) {
            update(`${Utils.formatUnits(amount, 18)} ${this.config.rewardToken} prize collected`);
          }
        }
      );

      this.ethereumService.provider!.on('block', currentBlock => {
        this.update().then(() => {});
      });
    }
  }
}
